import { Marker } from '@react-google-maps/api';
import customerOb from '../../../../../asset/images/customer_ob.svg';
import customerUncontracted from '../../../../../asset/images/customer_uncontracted.svg';
import { CustomerListType } from '../../../../../type/customer/customer.type';

export type CustomerMarkerProps = {
  customer: CustomerListType;
  callback: () => void;
};

export const CustomerMarker = (props: CustomerMarkerProps) => {
  const { customer, callback } = props;

  return (
    <Marker
      position={{
        lat: Number(customer.lat),
        lng: Number(customer.lng),
      }}
      onClick={(e) => {
        e.domEvent.preventDefault();
        e.domEvent.stopPropagation();
        if (callback) callback();
      }}
      icon={{
        url: customer.estimated_rank === 1 ? customerOb : customerUncontracted,
        scaledSize: new globalThis.google.maps.Size(100, 80),
        // scale: 1,
        // size: new globalThis.google.maps.Size(22, 22, 'px', px),
      }}
    />
  );
};
