/* eslint-disable no-irregular-whitespace */
import { useCallback, useState } from 'react';
import { Button } from 'semantic-ui-react';
import { CustomerListType } from '../../../../type/customer/customer.type';
import './customer-info-window.scss';
import { openEmail } from '../../../../utilities/open-email';
import { IconButton } from '../../button/icon-button/icon-button';
import { openTel } from '../../../../utilities/open-tel';

type CustomerInfoWindowProps = {
  customer: CustomerListType;
  callBack: () => void;
};

export const CustomerInfoWindow = (props: CustomerInfoWindowProps) => {
  const { customer, callBack } = props;
  const [detailFlag, setDetailFlag] = useState(false);

  const handleClickArrow = useCallback(() => {
    if (detailFlag) {
      setDetailFlag(false);
      callBack();
    } else {
      setDetailFlag(true);
    }
  }, [detailFlag]);

  const handleClickTel = useCallback(() => {
    openTel({ tel: customer.tel_no });
  }, []);

  const handleClickMail = useCallback(() => {
    openEmail({
      address: customer.address,
      /* TODO  */
      subject: '',
      body: ['Test'],
    });
  }, []);

  const handleClickSupportHistory = useCallback(() => {
    if (detailFlag) {
      setDetailFlag(false);
      callBack();
    } else {
      setDetailFlag(true);
    }
  }, [detailFlag]);

  const handleClickLine = useCallback(() => {
    /* TODO LINK */
    const pageLink = '\n https://www.marietta.co.jp/';
    const a = document.createElement('a');
    a.href = `https://line.me/R/share?text=${pageLink}`;
    document.body.appendChild(a);
    a.click();
    a.remove();
  }, []);

  return (
    <div className="CustomerInfoWindow">
      <div className="CustomerInfoWindow__arrow-btn" onClick={handleClickArrow}>
        <i className={`fas fa-angle-double-${!detailFlag ? 'up' : 'down'}`} />
      </div>
      {detailFlag ? (
        <div className="CustomerInfoWindow__info">
          <div className="CustomerInfoWindow__info__row">
            <div>
              {customer.name} {`(${customer.furigana})`}
            </div>
            <div>{customer.post_no}</div>
          </div>
          <div className="CustomerInfoWindow__info__row">{customer.prefecture + customer.city}</div>
          <div className="CustomerInfoWindow__info__row">{customer.address + customer.building_name}</div>
          <div className="CustomerInfoWindow__info__row">TEL : {customer.tel_no}</div>
          <div className="CustomerInfoWindow__info__row">
            {/* TODO Style */}
             : {customer.mail_address}<IconButton onClick={handleClickMail} fontAwesomeClass="fas fa-envelope" />
          </div>
          <div className="CustomerInfoWindow__info__row">LINE ID : {customer.line_id}</div>
          <div className="CustomerInfoWindow__info__row">
            Facebook ID :
            <a href={`https://www.facebook.com/${customer.facebook_id}`} target="_blank" rel="noopener noreferrer">
              {customer.facebook_id}
            </a>
          </div>
          <div className="CustomerInfoWindow__info__row"> :</div>
          <div className="CustomerInfoWindow__info__row">
              : {customer.sales_shop}
          </div>
          <div className="CustomerInfoWindow__info__row"> : {customer.sales_contact}</div>
        </div>
      ) : (
        <div className="CustomerInfoWindow__info">
          <div className="CustomerInfoWindow__info__row row1">
            <div className="customer-name">
              {customer.name} {`(${customer.furigana})`}
            </div>
            <div className="col">
              <div className="customer-rank">{customer.rank}</div>
              <div className="customer-type">{`${customer.estimated_rank === 1 ? 'OB' : ''}`}</div>
            </div>
          </div>
          <div className="CustomerInfoWindow__info__row row2">
            <div className="customer-address">
              {`${customer.post_no} ${customer.prefecture}${customer.city}`}
            </div>
          </div>
          <div className="CustomerInfoWindow__info__row row3">
            <div className="customer-tel">TEL : {customer.tel_no}</div>
            <div className="customer-salesStaff"> : {customer.sales_contact}</div>
          </div>
        </div>
      )}
      <div className="CustomerInfoWindow__footer">
        <Button onClick={handleClickTel}>
          <i className="fas fa-mobile-alt" />
        </Button>
        <Button onClick={handleClickSupportHistory}></Button>
        <Button onClick={handleClickLine}>
          <i className="fab fa-line" />
        </Button>
      </div>
    </div>
  );
};
