import { useCallback, useEffect, useState } from 'react';
import { useParams } from 'react-router-dom';
import { useDispatch, useSelector } from 'react-redux';
import { Button, Tab } from 'semantic-ui-react';
import { IconButton } from '../../../ui/button/icon-button/icon-button';
import { CustomerInfoTabContents } from '../../../ui/tab-contents/customer-info/customer-info-tab-contents';
import { FileListSP } from '../../layout/body/list/file/file-list';
import { MaintenanceListSP } from '../../layout/body/list/maintenance/maintenance-list';
import { PropositionListSP } from '../../layout/body/list/proposition-list/proposition-list';
import { SupportHistoryListSP } from '../../layout/body/list/support-history/support-history-list';
import { BasePageSP } from '../base.page.sp';
import './customer-detail.sp.scss';
import { CustomerActions } from '../../../../redux/customer/customer.action';
import { DialogActions } from '../../../../redux/dialog/dialog.action';
import { webShareApi } from '../../../../utilities/web-share';
import { openTel } from '../../../../utilities/open-tel';
import { State } from '../../../../redux/root.reducer';

export const CustomerDetailSP = () => {
  /* Hooks */
  const dispatch = useDispatch();

  /* TODO showType  */
  const [testShowType] = useState<0 | 1 |2>(0);
  const { customerID } = useParams<{ customerID: string; }>();
  const customerInfo = useSelector((state:State) => state.customer.customer);

  const panes = [
    { menuItem: '', render: () => <Tab.Pane><CustomerInfoTabContents /></Tab.Pane> },
    { menuItem: '', render: () => <Tab.Pane><PropositionListSP type={testShowType} /></Tab.Pane> },
    { menuItem: '', render: () => <Tab.Pane><SupportHistoryListSP type={testShowType} /></Tab.Pane> },
    { menuItem: '', render: () => <Tab.Pane><FileListSP /></Tab.Pane> },
    { menuItem: '', render: () => <Tab.Pane><MaintenanceListSP type={testShowType} /></Tab.Pane> },
  ];

  /* Callback */
  const handleClickShare = useCallback(
    () => {
      webShareApi({
        url: 'https://www.marietta.co.jp/',
        callbackError: () => {
          dispatch(DialogActions.pushMessage({
            message: [''],
          }));
        },
      });
    },
    [],
  );

  const handleClickTel = useCallback(() => {
    if (customerInfo?.tel_no) { openTel({ tel: customerInfo.tel_no }); }
  }, [customerInfo?.tel_no]);

  const handleClickUpload = useCallback(() => {
    dispatch(DialogActions.pushReady());
  }, []);

  const handleRegistrationSupport = useCallback(() => {
    dispatch(DialogActions.pushReady());
  }, []);

  const handleClickSort = useCallback(() => {
    dispatch(DialogActions.pushReady());
  }, []);

  /* Effect */
  useEffect(() => {
    dispatch(CustomerActions.api.customer.get({
      param: {
        id: 1,
      },
    }));
    /* UnMount */
    return () => {
      dispatch(CustomerActions.setCustomer(null));
    };
  }, [customerID]);

  return (
    <BasePageSP>
      <div className="CustomerDetailSP">
        <div className="CustomerDetailSP__body">

          {/* body header */}
          <div className="CustomerDetailSP__body__header">
            <span>
              
            </span>
            {/* TODO  */}
            <Button className="btn-color-set1" onClick={handleClickSort}></Button>
          </div>

          {/* body contents */}
          <div className="CustomerDetailSP__body__contents">
            <Tab panes={panes} />
          </div>

        </div>

        <footer className="CustomerDetailSP__footer">
          <div className="button-wrapper share">
            <IconButton
              fontAwesomeClass="fas fa-share-alt"
              onClick={handleClickShare}
            />
          </div>
          <div className="button-wrapper phone">
            <IconButton
              fontAwesomeClass="fas fa-mobile-alt"
              onClick={handleClickTel}
            />
          </div>
          <div className="button-wrapper upload">
            <IconButton
              fontAwesomeClass="fas fa-file-upload"
              onClick={handleClickUpload}
            />
          </div>
          <div className="button-wrapper regist-support">
            <Button onClick={handleRegistrationSupport}></Button>
          </div>
        </footer>
      </div>
    </BasePageSP>
  );
};
