export type EmailParm = {
  address: string;
  bcc?: string[];
  cc?: string[];
  subject?: string;
  body?: string[];
};

/**
 * 
 * @param param
 * @returns
 */
export const openEmail = (param: EmailParm) => {
  const lineFeed = '\r\n';

  const mailTo = `mailto:${param.address}`;
  const cc = param.cc ? `?cc=${param.cc.reduce((v, stack) => `${stack + v},`)}` : '';
  const bcc = param.bcc ? `${cc ? '&' : '?'}cc=${param.bcc.reduce((v, stack) => `${stack + v},`)}` : '';
  const subject = param.subject ? `${cc || bcc ? '&' : '?'}subject=${param.subject}` : '';
  const body = param.body ? `${cc || bcc || subject ? '&' : '?'}body=${param.body.reduce((v, stack) => stack + v + lineFeed)}` : '';

  const a = document.createElement('a');
  a.href = `${mailTo}${cc}${bcc}${subject}${body}`;
  document.body.appendChild(a);
  a.click();
  a.remove();
};
