import { push } from 'connected-react-router';
import {
  useCallback, useState, useRef, useEffect, RefObject,
} from 'react';
import { Button, Input } from 'semantic-ui-react';
import { useDispatch } from 'react-redux';
import { RoutingPath } from '../../../../routes/routing-pass';
import './header.sp.scss';
import { UserInfoData } from './header.sp.type';
import { AuthActions } from '../../../../redux/auth/auth.action';
import { DialogActions } from '../../../../redux/dialog/dialog.action';

type Props = {
  userInfo: UserInfoData;
  headerRef?: RefObject<HTMLElement>
}

export const HeaderSP = (props: Props) => {
  const dispatch = useDispatch();
  const { userInfo, headerRef } = props;

  /* ref */
  const menuHeaderEle = useRef<HTMLDivElement>(null);

  /* state */
  const [isMenuOpened, setIsMenuOpened] = useState<boolean>(false);
  const [menuHeaderHeight, setMenuHeaderHeight] = useState<number>(0);

  /* callback */
  const handleClickMenu = useCallback(
    (routingPath: string) => {
      dispatch(push(routingPath));
    },
    [],
  );

  const handleClickLogout = useCallback(
    () => {
      dispatch(DialogActions.pushMessage({
        message: [''],
        btnProps: [
          {
            label: '',
            callback: () => {
              dispatch(DialogActions.pop());
            },
            isCancel: true,
          },
          {
            label: 'OK',
            callback: () => {
              dispatch(AuthActions.api.logout());
            },
            isCancel: true,
          },
        ],
      }));
    },
    [],
  );

  /* effect */
  useEffect(() => {
    setMenuHeaderHeight(menuHeaderEle.current?.getBoundingClientRect().height || 0);
  }, [menuHeaderEle]);

  return (
    <header
      className="headerSP"
      id="headerSP"
      ref={headerRef}
    >
      <div
        className="headerSP__menu-btn"
        onClick={() => setIsMenuOpened(!isMenuOpened)}
      >
        <i className="fas fa-bars" />
      </div>
      <div className="headerSP__search">
        <Input />
      </div>
      <div
        className="headerSP__search-box-btn"
        onClick={() => {}}
      >
        <i className="fas fa-search-plus" />
      </div>

      <div className={`headerSP__menu-content ${isMenuOpened ? 'opened' : ''}`}>
        <div className="headerSP__menu-content__header" ref={menuHeaderEle}>
          <span className="headerSP__menu-content__header__greeting">
             
          </span>
          <div
            className="headerSP__menu-content__header__close"
            onClick={() => setIsMenuOpened(false)}
          >
            <i className="fas fa-times" />
          </div>
        </div>

        <div
          className="headerSP__menu-content__body"
          style={{
            top: `${menuHeaderHeight}px`,
            height: `calc(100% - ${menuHeaderHeight}px)`,
          }}
        >
          <div className="headerSP__menu-content__body__main-menu">
            <div className="headerSP__menu-content__body__main-menu__btn customer-list">
              <Button onClick={() => handleClickMenu(RoutingPath.customer)}></Button>
            </div>
            <div className="headerSP__menu-content__body__main-menu__btn customer-regist">
              <Button onClick={() => handleClickMenu(RoutingPath.customer)}></Button>
            </div>
            <div className="headerSP__menu-content__body__main-menu__btn customer-search">
              <Button onClick={() => handleClickMenu(RoutingPath.customer)}></Button>
            </div>
            <div className="headerSP__menu-content__body__main-menu__btn file">
              <Button onClick={() => handleClickMenu(RoutingPath.file)}></Button>
            </div>
            <div className="headerSP__menu-content__body__main-menu__btn support-regist">
              <Button onClick={() => handleClickMenu(RoutingPath.supportHistory)}></Button>
            </div>
          </div>
          <div className="headerSP__menu-content__body__menu-list">
            <div className="menu">
              <div className="menu__category"></div>
              <div
                className="menu__item"
                onClick={() => handleClickMenu(RoutingPath.customer)}
              >
                
              </div>
              <div
                className="menu__item"
                onClick={() => handleClickMenu(RoutingPath.customer)}
              >
                
              </div>
            </div>
            <div className="menu">
              <div className="menu__category"></div>
              <div
                className="menu__item"
                onClick={() => handleClickMenu(RoutingPath.proposition)}
              >
                
              </div>
              <div
                className="menu__item"
                onClick={() => handleClickMenu(RoutingPath.proposition)}
              >
                
              </div>
            </div>
            <div className="menu">
              <div className="menu__category"></div>
              <div
                className="menu__item"
                onClick={() => handleClickMenu(RoutingPath.estimate)}
              >
                
              </div>
              <div
                className="menu__item"
                onClick={() => handleClickMenu(RoutingPath.estimate)}
              >
                
              </div>
            </div>
            <div className="menu">
              <div className="menu__category"></div>
              <div
                className="menu__item"
                onClick={() => handleClickMenu(RoutingPath.maintenance)}
              >
                
              </div>
              <div
                className="menu__item"
                onClick={() => handleClickMenu(RoutingPath.maintenance)}
              >
                
              </div>
            </div>
            <div className="menu">
              <div className="menu__category"></div>
              <div
                className="menu__item"
                onClick={() => handleClickMenu(RoutingPath.file)}
              >
                
              </div>
              <div
                className="menu__item "
                onClick={() => handleClickMenu(RoutingPath.file)}
              >
                
              </div>
            </div>
            <div className="menu">
              <div className="menu__category"></div>
              <div
                className="menu__item "
                onClick={() => handleClickMenu(RoutingPath.supportHistory)}
              >
                
              </div>
              <div
                className="menu__item "
                onClick={() => handleClickMenu(RoutingPath.supportHistory)}
              >
                
              </div>
            </div>
            <div className="menu">
              <div className="menu__category"></div>
              <div className="menu__user-info">
                <div className="menu__user-info__item">CD{userInfo.employeeCD}</div>
                <div className="menu__user-info__item">{userInfo.storeName}</div>
                <div className="menu__user-info__item">
                  {userInfo.userName}{userInfo.userAltName && userInfo.userAltName}
                </div>
                <div className="menu__user-info__item">{userInfo.userFurigana}</div>
                <div className="menu__user-info__item">{userInfo.jobPost}</div>
                <div className="menu__user-info__item">{userInfo.email}</div>
                <Button className="menu__user-info__remake-pass"></Button>
              </div>
            </div>
            <div className="menu">
              <div
                className="menu__item logout"
                onClick={handleClickLogout}
              >
                
              </div>
            </div>
          </div>
        </div>
      </div>
    </header>
  );
};
