import {
  useCallback, useEffect, useRef, useState,
} from 'react';
import { useDispatch } from 'react-redux';
import { LeftIconButton } from '../../../ui/button/left-icon-button/left-icon-button';
import { CustomerListSP } from '../../layout/body/list/customer-list/customer-list.sp';
import { BasePageSP } from '../base.page.sp';
import './customer.sp.scss';
import customerObW from '../../../../asset/images/customer_ob_w.svg';
import customerUncontractedW from '../../../../asset/images/customer_uncontracted_w.svg';
import { LeftImgButton } from '../../../ui/button/left-img-button/left-img-button';
import { DialogActions } from '../../../../redux/dialog/dialog.action';
import { CustomerActions } from '../../../../redux/customer/customer.action';
import { MapBase } from '../../../ui/map/map-base';
import { SearchAddressDialog } from '../../../ui/map/search-address-dialog.tsx/search-address-dialog';

export const CustomerSP = () => {
  const dispatch = useDispatch();

  const [showType, setShowType] = useState<'map' | 'list'>('map');
  const [customerShowType, setCustomerShowType] = useState<0 | 1 |2>(0);

  const [footerHeight, setFooterHeight] = useState<number>(0);
  const footerEle = useRef<HTMLElement>(null);

  /* callback */
  const handleClickAddressSearch = useCallback(
    () => {
      dispatch(DialogActions.push({
        title: '',
        element: <SearchAddressDialog />,

        /* TODO Test */
        // element: <RegistrationAddressMapDialog
        //   callback={(address) => {
        //     window.console.log('address ', address);
        //   }}
        // />,
      }));
    },
    [],
  );

  /* effect */
  useEffect(() => {
    setFooterHeight(footerEle.current?.getBoundingClientRect().height || 0);
  }, [footerEle]);

  useEffect(() => {
    dispatch(CustomerActions.api.customer.getList({}));
  }, []);

  return (
    <BasePageSP className="CustomerSP">
      <div
        className="MapSP"
        style={{ height: `calc(100% - ${footerHeight}px)` }}
      >
        <div
          className="MapSP__body"
        >
          <div className="MapSP__body__map" />
          {showType === 'map'
            ? (
              <MapBase
                customerOption={{
                  type: customerShowType,
                }}
                isNowPoint
                searchOption={{}}
              />
            )
            : <CustomerListSP type={customerShowType} />}

          {showType === 'map'
          && (
          <LeftIconButton
            label=""
            fontAwesomeClass="fas fa-map"
            className="search-address"
            onClick={handleClickAddressSearch}
          />
          )}
          {/* <Button label="" className="search-address" onClick={() => {}} /> */}
          <LeftIconButton
            label={`${showType === 'map' ? '' : ''}`}
            fontAwesomeClass="fas fa-sync-alt"
            className="change-display"
            onClick={() => setShowType(showType === 'map' ? 'list' : 'map')}
          />
        </div>

        <footer className="MapSP__footer" ref={footerEle}>
          <div className="MapSP__footer__row row1">
            <LeftImgButton
              label=""
              iconSrc={[customerObW, customerUncontractedW]}
              className="btn btn-filter-all"
              onClick={() => setCustomerShowType(0)}
            />
            <LeftImgButton
              label="OB"
              iconSrc={customerObW}
              className="btn ob"
              onClick={() => setCustomerShowType(1)}
            />
            <LeftImgButton
              label=""
              iconSrc={customerUncontractedW}
              className="btn uncontracted"
              onClick={() => setCustomerShowType(2)}
            />
          </div>
          <div className="MapSP__footer__row row2">
            <LeftIconButton
              label=""
              fontAwesomeClass="fas fa-search"
              className="btn search-detail"
              onClick={() => {}}
            />
            <LeftIconButton
              label=""
              fontAwesomeClass="far fa-edit"
              className="btn regist"
              onClick={() => {}}
            />
          </div>
        </footer>
      </div>
    </BasePageSP>
  );
};
