import actionCreatorFactory from 'typescript-fsa';
import { MasterEstimateDummy } from '../../../../collection/dummy/master/master-estimate.dummy';
import { ApiBase } from '../../../../service/api-base';
import { RequestBaseCollection } from '../../../../service/api.collection';
import { RequestBaseParam } from '../../../../type/api.type';
import {
  ApiMasterEstimatePostParam,
  ApiMasterEstimateGetListParam,
  ApiMasterEstimateGetListResponse,
} from './api-master-estimate.type';

export const ActionCreator = actionCreatorFactory('master/api/estimate');

export const apiMasterEstimate = {
  post: ActionCreator<{param:ApiMasterEstimatePostParam, callback:()=>void}>('post'),
  getList: ActionCreator<ApiMasterEstimateGetListParam>('get/list'),
};

export class ApiMasterEstimate<T = any> extends ApiBase<T> {
  constructor(param: RequestBaseParam & {id?: number}) {
    const { id } = param;
    super({
      ...RequestBaseCollection(),
      ...param,
      url: `/api/master/fixed/quotefixed${id ? `/${id}` : ''}`,
    });
  }
}

export class ApiMasterEstimatePost extends ApiMasterEstimate {
  constructor(param: ApiMasterEstimatePostParam) {
    const { data, id } = param;
    super({
      httpMethod: 'POST',
      param: data,
      id,
    });
  }
}

export class ApiMasterEstimateGetList
  extends ApiMasterEstimate<ApiMasterEstimateGetListResponse> {
  constructor(param: ApiMasterEstimateGetListParam) {
    const { data } = param;
    super({
      httpMethod: 'GET',
      param: data,
    });
    this.dummyData = MasterEstimateDummy;
  }
}
