import { Button } from 'semantic-ui-react';
import { IconButtonProps } from '../icon-button.type';
import './left-icon-button.scss';

export const LeftIconButton = (props: IconButtonProps) => {
  const {
    label,
    fontAwesomeClass,
    onClick,
    disabled,
    className,
  } = props;

  return (
    <Button onClick={onClick} disabled={disabled} className={`LeftIconButton ${className}`}>
      <i className={`LeftIconButton__icon ${fontAwesomeClass}`} />
      <span>
        {label}
      </span>
    </Button>
  );
};
