import { Button, ButtonProps } from 'semantic-ui-react';
import './left-img-button.scss';

type Props = {
  iconSrc: string | string[]
} & ButtonProps

export const LeftImgButton = (props: Props) => {
  const {
    label,
    iconSrc,
    onClick,
    disabled,
    className,
  } = props;

  return (
    <Button
      onClick={onClick}
      disabled={disabled}
      className={`LeftImgButton ${className}`}
    >
      <div className="LeftImgButton__img-wrapper img-wrapper">
        {Array.isArray(iconSrc)
          ? iconSrc.map((v, i) => (
            <img key={`img${i}`} src={v} alt="" className="LeftImgButton__img" />
          ))
          : (<img src={iconSrc} alt="" className="LeftImgButton__img" />)}
      </div>
      <span>
        {label}
      </span>
    </Button>
  );
};
