import { MasterLabel } from '../master.type';

type Props = {
  callback: (type: number) => void,
  list: MasterLabel[],
}

export const SideMenu = (props: Props) => {
  const { callback, list } = props;

  return (
    <>
      {list.map((v, i) => (
        <div key={`sideMenu ${i}`}>
          <button onClick={() => callback(v.type)}>{v.label}</button>
        </div>
      ))}

    </>
  );
};
