import { InfoWindow, Marker } from '@react-google-maps/api';
import { useCallback, useEffect, useState } from 'react';
import { useDispatch } from 'react-redux';
import { Button } from 'semantic-ui-react';
import { MapActions } from '../../../../../redux/map/map.action';
import { Position, Size, Address } from '../../../../../type/map/map.type';

export type SearchMarkerProps = {
  position: Position;
  address: Address | null;
  option?: {
    pixelOffset?: Size;
    isRegistrationAddress?: {
      callback: (address:Address | null) => void;
    };
  };
};

export const SearchMarker = (props: SearchMarkerProps) => {
  const dispatch = useDispatch();
  const { position, address, option } = props;

  const [isInfo, setIsInfo] = useState(true);
  const [imgUrl, setImgUrl] = useState('');

  useEffect(() => {
    if (option?.isRegistrationAddress) return;
    dispatch(
      MapActions.api.getLocationImg({
        param: {
          size: { h: 100, w: 100 },
          location: position,
        },
        callback: (url) => {
          setImgUrl(url || '');
        },
      }),
    );
  }, [position]);

  const handleClickRegistration = useCallback(
    () => {
      if (!option?.isRegistrationAddress) return;
      option.isRegistrationAddress.callback(address);
    },
    [address],
  );

  return (
    <>
      <Marker onClick={() => setIsInfo(true)} position={position} />
      {isInfo && (
        <InfoWindow position={position} onCloseClick={() => setIsInfo(false)} options={option}>
          <div>
            <div>{address?.formattedAddress}</div>
            <a
              href={`https://maps.google.co.jp/maps?q=${position.lat},${position.lng}`}
              target="_blank"
              rel="noreferrer"
            >
              GoogleMap
            </a>
            {!option?.isRegistrationAddress
              ? <div className="imgBox">{imgUrl && <img src={imgUrl} alt="" />}</div>
              /* TODO   */
              : <div> <Button onClick={handleClickRegistration}></Button> </div>}
          </div>
        </InfoWindow>
      )}
    </>
  );
};
