import actionCreatorFactory from 'typescript-fsa';
import { MasterKouziCheckDummy } from '../../../../collection/dummy/master/master-kouzi-check.dummy';
import { ApiBase } from '../../../../service/api-base';
import { RequestBaseCollection } from '../../../../service/api.collection';
import { RequestBaseParam } from '../../../../type/api.type';
import {
  ApiMasterKouziCheckPostParam,
  ApiMasterKouziCheckGetListParam,
  ApiMasterKouziCheckGetListResponse,
} from './api-master-kouzi-check.type';

export const ActionCreator = actionCreatorFactory('master/api/kouzi/check');

export const apiMasterKouziCheck = {
  post: ActionCreator<{param:ApiMasterKouziCheckPostParam, callback:()=>void}>('post'),
  getList: ActionCreator<ApiMasterKouziCheckGetListParam>('get/list'),
};

export class ApiMasterKouziCheck<T = any> extends ApiBase<T> {
  constructor(param: RequestBaseParam & {id?: number}) {
    const { id } = param;
    super({
      ...RequestBaseCollection(),
      ...param,
      url: `/api/master/company/kouziCheck${id ? `/${id}` : ''}`,
    });
  }
}

export class ApiMasterKouziCheckPost extends ApiMasterKouziCheck {
  constructor(param: ApiMasterKouziCheckPostParam) {
    const { data, id } = param;
    super({
      httpMethod: 'POST',
      param: data,
      id,
    });
  }
}

export class ApiMasterKouziCheckGetList
  extends ApiMasterKouziCheck<ApiMasterKouziCheckGetListResponse> {
  constructor(param: ApiMasterKouziCheckGetListParam) {
    const { data } = param;
    super({
      httpMethod: 'GET',
      param: data,
    });
    this.dummyData = MasterKouziCheckDummy;
  }
}
