import { useCallback } from 'react';
import { useDispatch, useSelector } from 'react-redux';
import { Button, Modal } from 'semantic-ui-react';
import { DialogActions } from '../../redux/dialog/dialog.action';
import { State } from '../../redux/root.reducer';
import { ConvertLineFeed } from '../../utilities/line-feed-conversion';
import './dialog.scss';
import { DialogProps } from '../../type/dialog.type';

export const BaseDialog = (props: DialogProps & {isTop:boolean}) => {
  const dispatch = useDispatch();

  const {
    title,
    element,
    className,
    messageType,
    isTop,
    onCloseClick,
  } = props;

  const handleClickClose = useCallback(
    () => {
      if (onCloseClick) onCloseClick();
      dispatch(DialogActions.pop());
    },
    [],
  );

  return (
    <Modal
      open
      className={className}
      dimmer={{ className: isTop ? 'top-dimmer' : 'under-dimmer' }}
    >
      <Modal.Header>{title || ''} {!messageType
        && (
        <div onClick={handleClickClose} style={{ textAlign: 'right' }}>
          <i className="fas fa-times" />
        </div>
        )}
      </Modal.Header>
      <Modal.Content>
        {messageType ? (
          <>
            <div>
              <ConvertLineFeed className="" text={messageType.messages || []} />
            </div>
            <div>
              {messageType.btnProps?.map((v, i) => (
                <Button
                  key={`btn${i}`}
                  onClick={v.callback}
                  className={v.isCancel ? 'cancel_btn' : ''}
                >
                  {v.label || 'OK'}
                </Button>
                // TODO STYLE
              ))}
            </div>
          </>
        ) : element}
      </Modal.Content>
    </Modal>
  );
};

export const Dialog = () => {
  const dialogs = useSelector((state: State) => state.dialog.dialogs);
  return (
    <>
      {dialogs.map((v, i) => (
        <div key={`dialog${i}`}>
          <BaseDialog
            isTop={i === dialogs.length - 1}
            title={v.title}
            messageType={v.messageType}
            element={v.element}
            message={v.messageType?.messages}
            btnProps={v.messageType?.btnProps}
          />
        </div>
      ))}
    </>
  );
};
