import actionCreatorFactory from 'typescript-fsa';
import { MasterOrderFailureDummy } from '../../../../collection/dummy/master/master-order-failure.dummy';
import { ApiBase } from '../../../../service/api-base';
import { RequestBaseCollection } from '../../../../service/api.collection';
import { RequestBaseParam } from '../../../../type/api.type';
import {
  ApiMasterOrderFailurePostParam,
  ApiMasterOrderFailureGetListParam,
  ApiMasterOrderFailureGetListResponse,
} from './api-master-order-failure.type';

export const ActionCreator = actionCreatorFactory('master/api/order-failure-reason');

export const apiMasterOrderFailure = {
  post: ActionCreator<{param:ApiMasterOrderFailurePostParam, callback:()=>void}>('post'),
  getList: ActionCreator<ApiMasterOrderFailureGetListParam>('get/list'),
};

export class ApiMasterOrderFailure<T = any> extends ApiBase<T> {
  constructor(param: RequestBaseParam & {id?: number}) {
    const { id } = param;
    super({
      ...RequestBaseCollection(),
      ...param,
      url: `/api/master/kubun/lostorder${id ? `/${id}` : ''}`,
    });
  }
}

export class ApiMasterOrderFailurePost extends ApiMasterOrderFailure {
  constructor(param: ApiMasterOrderFailurePostParam) {
    const { data, id } = param;
    super({
      httpMethod: 'POST',
      param: data,
      id,
    });
  }
}

export class ApiMasterOrderFailureGetList
  extends ApiMasterOrderFailure<ApiMasterOrderFailureGetListResponse> {
  constructor(param: ApiMasterOrderFailureGetListParam) {
    const { data } = param;
    super({
      httpMethod: 'GET',
      param: data,
    });
    this.dummyData = MasterOrderFailureDummy;
  }
}
