import { useEffect, useCallback } from 'react';
import { useDispatch, useSelector } from 'react-redux';
import { push } from 'connected-react-router';
import { CustomerCard } from '../../../../../ui/card/customer-card/customer-card';
import './customer-list.sp.scss';
import { State } from '../../../../../../redux/root.reducer';
import { CustomerActions } from '../../../../../../redux/customer/customer.action';
import { RoutingPath } from '../../../../../../routes/routing-pass';

type Props = {
    type: 0 | 1 | 2 ;
}

export const CustomerListSP = (props: Props) => {
  const { type } = props;

  /* Hooks */
  const customerListData = useSelector((state: State) => state.customer.list);
  const dispatch = useDispatch();

  /* Effect */
  useEffect(() => {
    dispatch(CustomerActions.api.customer.getList({}));
  }, []);

  /* List */
  const customerList = !type ? customerListData : customerListData.filter(
    (v) => v.estimated_rank === type,
  );

  const handleClickCard = useCallback((id: number) => {
    dispatch(push(`${RoutingPath.customerDetail}/${id}`));
  }, []);

  return (
    <div className="CustomerListSP">
      {customerList.map((v, i) => (
        <div key={`card${i}`} className="card-wrapper">
          <CustomerCard customerData={v} onClick={handleClickCard} isSP />
        </div>
      ))}
    </div>
  );
};
