function* tryStoreGetList(action: ReturnType<typeof MasterActions.api.store.getList>) {
  const request = new ApiMasterStoreGetList(action.payload);
  try {
    const result: ResultType<ApiMasterStoreGetListResponse> = yield request.run();
    if (ApiBase.isSuccess(result)) {
      yield put(MasterActions.setStoreList(result.body.data));
    } else {
      throw new Error();
    }
  } catch (e) {
    window.console.error(e);
  }
}

function* tryStorePost(action: ReturnType<typeof MasterActions.api.store.post>) {
  const { param, callback } = action.payload;
  const request = new ApiMasterStorePost(param);
  try {
    const result: ResultType = yield request.run();
    if (ApiBase.isSuccess(result)) {
      yield put(DialogActions.pushMessage({
        message: [''],
        btnProps: [
          {
            label: 'OK',
            callback: () => {
              callback();
              Store.dispatch(DialogActions.clear());
            },
          },
        ],
      }));
    } else {
      yield put(DialogActions.pushMessage({
        message: [''],
        btnProps: [
          {
            label: 'OK',
            callback: () => {
              Store.dispatch(DialogActions.pop());
            },
          },
        ],
      }));
    }
  } catch (e) {
    window.console.error(e);
  }
}

