import actionCreatorFactory from 'typescript-fsa';
import { MasterGenbaPreparationDummy } from '../../../../collection/dummy/master/master-genba-preparation.dummy';
import { ApiBase } from '../../../../service/api-base';
import { RequestBaseCollection } from '../../../../service/api.collection';
import { RequestBaseParam } from '../../../../type/api.type';
import {
  ApiMasterGenbaPreparationPostParam,
  ApiMasterGenbaPreparationGetListParam,
  ApiMasterGenbaPreparationGetListResponse,
} from './api-master-genba-preparation.type';

export const ActionCreator = actionCreatorFactory('master/api/genba/preparation');

export const apiMasterGenbaPreparation = {
  post: ActionCreator<{param:ApiMasterGenbaPreparationPostParam, callback:()=>void}>('post'),
  getList: ActionCreator<ApiMasterGenbaPreparationGetListParam>('get/list'),
};

export class ApiMasterGenbaPreparation<T = any> extends ApiBase<T> {
  constructor(param: RequestBaseParam & {id?: number}) {
    const { id } = param;
    super({
      ...RequestBaseCollection(),
      ...param,
      url: `/api/master/company/store${id ? `/${id}` : ''}`,
    });
  }
}

export class ApiMasterGenbaPreparationPost extends ApiMasterGenbaPreparation {
  constructor(param: ApiMasterGenbaPreparationPostParam) {
    const { data, id } = param;
    super({
      httpMethod: 'POST',
      param: data,
      id,
    });
  }
}

export class ApiMasterGenbaPreparationGetList
  extends ApiMasterGenbaPreparation<ApiMasterGenbaPreparationGetListResponse> {
  constructor(param: ApiMasterGenbaPreparationGetListParam) {
    const { data } = param;
    super({
      httpMethod: 'GET',
      param: data,
    });
    this.dummyData = MasterGenbaPreparationDummy;
  }
}
