import './map-base.scss';

import {
  GoogleMap,
  GoogleMapProps,
} from '@react-google-maps/api';
import { useCallback, useEffect, useState } from 'react';
import { useDispatch, useSelector } from 'react-redux';
import { SystemActions } from '../../../redux/system/system.action';
import { CustomerListType } from '../../../type/customer/customer.type';
import { CustomerInfoWindow } from '../info-window/customer-info-window/customer-info-window';
import { CustomerMarker } from './marker/customer-marker/customer-marker';
import { MapActions } from '../../../redux/map/map.action';
import { HumanMarker } from './marker/human-marker/human-marker';
import { State } from '../../../redux/root.reducer';
import { SearchMarker } from './marker/search-marker/search-marker';
import {
  MapMouseEvent, MapType, Size, Address,
} from '../../../type/map/map.type';
import { CustomerActions } from '../../../redux/customer/customer.action';

export const ApiKey = 'AIzaSyDd4kZOB96LZ9GMxmDUiHcwriQEb1KA9-w';

export const testCenter = {
  lat: 35.69775,
  lng: 139.77521,
};

export type MapProps = {
  customerOption?: {
    type: number;
  },
  propositionOption?: {},
  searchOption?: {
    isRegistrationAddress?: {
      callback: (address: Address | null) => void;
    };
  },
  isNowPoint?: boolean;
} & GoogleMapProps;

export const MapBase = (props: MapProps) => {
  const {
    children,
    customerOption,
    isNowPoint,
    searchOption,
    onLoad,
    onTilesLoaded,
    onClick,
  } = props;

  /* Hooks */
  const { searchPos, searchAddress, centerPos } = useSelector((state:State) => state.map);
  const { list } = useSelector((state:State) => state.customer);
  const dispatch = useDispatch();

  /* State */
  const [size, setSize] = useState<undefined | Size>(undefined);
  // Customer
  const [customer, setCustomer] = useState<CustomerListType | null>(null);
  const [customerInfo, setCustomerInfo] = useState(false);
  const customerList = !customerOption?.type
    ? list
    : list.filter((v) => v.estimated_rank === customerOption.type);

  /* Callback */
  const createOffsetSize = useCallback(
    () => {
      setSize(new window.google.maps.Size(0, -45));
    },
    [setSize],
  );

  const handleOnLoad = useCallback(
    (map:MapType) => {
      dispatch(SystemActions.isLoading(true));
      createOffsetSize();
      if (onLoad) onLoad(map);
    },
    [onLoad],
  );

  const handleOnTilesLoaded = useCallback(
    () => {
      dispatch(SystemActions.isLoading(false));
      if (onTilesLoaded) onTilesLoaded();
    },
    [onTilesLoaded],
  );

  /* Effect */
  useEffect(() => {
    if (customer) { dispatch(CustomerActions.api.customer.getList({})); }
  }, []);

  const handleOnClick = useCallback(
    (e:MapMouseEvent) => {
      dispatch(SystemActions.isLoading(false));

      if (customerOption) {
        setCustomerInfo(false);
        dispatch(MapActions.setSearchAddress(null));
        dispatch(MapActions.setSearchPos(null));
      }

      if (searchOption?.isRegistrationAddress) {
        if (!e.latLng) return;
        if (searchPos) {
          dispatch(MapActions.setSearchPos(null));
          dispatch(MapActions.setSearchAddress(null));
          return;
        }
        const res = new globalThis.google.maps.LatLng(e.latLng.lat(), e.latLng.lng());
        dispatch(MapActions.api.geocoder({
          param: {
            location: res,
          },
        }));
      }

      if (onClick) onClick(e);
    },
    [onClick, searchOption, searchPos, setCustomerInfo],
  );

  return (
    <>
      <GoogleMap
        options={
          {
            // mapTypeControl: false,
            // // streetViewControl: false,
            // fullscreenControl: false,
            // zoomControl: false,
            zoomControlOptions: {
              position: 7.0,
            },
            streetViewControlOptions: {
              position: 7.0,
            },
          }
        }
        mapContainerStyle={{
          width: '100%',
          height: '100%',
        }}
        center={centerPos || testCenter}
        clickableIcons={false}
        zoom={15}
        onLoad={handleOnLoad}
        onTilesLoaded={handleOnTilesLoaded}
        onClick={handleOnClick}
      >
        {/* GoogleDiv */}
        <div
          className="icon_cover"
          onClick={(e) => e.stopPropagation()}
        />

        {/*  */}
        {isNowPoint && <HumanMarker position={testCenter} />}

        {/*  */}
        {(searchOption && searchPos) && (
          <SearchMarker
            position={searchPos}
            address={searchAddress}
            option={searchOption.isRegistrationAddress ? {
              pixelOffset: size,
              isRegistrationAddress: {
                callback: (address) => {
                  searchOption.isRegistrationAddress?.callback(address);
                },
              },
            } : {
              pixelOffset: size,
            }}
          />
        )}

        {/*  */}
        {customerOption && customerList.map((v, i) => (
          <CustomerMarker
            key={`customer-marker${i}`}
            customer={v}
            callback={() => {
              setCustomer(v);
              setCustomerInfo(true);
            }}
          />
        ))}
        {children}
      </GoogleMap>

      {/* Info */}
      {customerInfo && customer && (
      <CustomerInfoWindow
        customer={customer}
        callBack={() => { }}
      />
      )}
    </>
  );
};
