/* eslint-disable no-irregular-whitespace */
import { useCallback } from 'react';
import { CustomerListType } from '../../../../type/customer/customer.type';
import './customer-card.scss';

type Props = {
    onClick: (id:number)=>void,
    customerData: CustomerListType,
    className?: string,
    isSP?: boolean
}

export const CustomerCard = (props: Props) => {
  const {
    onClick, customerData, className, isSP,
  } = props;

  /* Callback */
  const handleClickCustomerDetail = useCallback(
    () => {
      // dispatch(push(`${RoutingPath.customerDetail}/${customerData.id}`));
      onClick(customerData.id);
    }, [onClick],
  );

  const baseClassName = 'CustomerCard';
  const defaultClassName = isSP ? `${baseClassName}-sp` : `${baseClassName}`;
  const typeName = customerData.estimated_rank && (customerData.estimated_rank === 1 ? 'OB' : '');

  return (
    <div className={`${defaultClassName} ${className}`} onClick={handleClickCustomerDetail}>
      <div className={`${defaultClassName}__row row1`}>
        <div className="customer-name">{`${customerData.name}${customerData.furigana}`}</div>
        <div className="col">
          <div className="customer-rank"><span>{customerData.rank}</span></div>
          <div className="customer-type"><span>{typeName}</span></div>
        </div>
      </div>
      <div className={`${defaultClassName}__row row2`} style={{ display: 'flex' }}>
        <div className="customer-postal">{`${customerData.post_no}`}</div>
        <div className="customer-address">{`${customerData.prefecture}${customerData.city}${customerData.address}`}</div>
      </div>
      <div className={`${defaultClassName}__row row3`}>
        <div className="customer-tel">TEL{customerData.tel_no}</div>
        <div className="customer-salesStaff">{customerData.sales_contact}</div>
      </div>
    </div>
  );
};

CustomerCard.defaultProps = { isSP: false };
