import './base.page.sp.scss';
import {
  ReactElement, useEffect, useRef, useState,
} from 'react';
import { HeaderSP } from '../layout/header/header.sp';

export type BaseProps = {
    className?: string,
    children?: ReactElement | ReactElement[],
};

export const BasePageSP = (props: BaseProps) => {
  const headerEle = useRef<HTMLElement>(null);
  const [headerHeight, setHeaderHeight] = useState<number>(0);
  const {
    className,
    children,
  } = props;

  /* effect */
  useEffect(() => {
    setHeaderHeight(headerEle.current?.getBoundingClientRect().height || 0);
  }, [headerEle]);

  return (
    <div className={`PageBaseSP ${className}`}>
      {/* - header - */}
      <HeaderSP
        userInfo={{
          employeeCD: '9999',
          storeName: 'ABC',
          userName: '',
          userAltName: '',
          userFurigana: '',
          jobPost: '',
          email: 'demo-taro@demo.co.jp',
        }}
        headerRef={headerEle}
      />

      {/* - body - */}
      <div
        className="PageBaseSP__body"
        style={{ height: `calc(100% - ${headerHeight}px)` }}
      >
        {children}
      </div>
    </div>
  );
};
