import './top-label-input-field.scss';
import { Input, InputProps } from 'semantic-ui-react';

type Props = {
  isSP?: true;
  onEnterKeyPress?: () => void;
} &InputProps;

export const TopLabelInputField = (props: Props) => {
  const {
    label, className, isSP, type, placeholder, onChange, disabled, onEnterKeyPress, value,
  } = props;

  const baseClassName = 'TopLabelInputField';
  const defaultClass = isSP ? `${baseClassName}-sp` : `${baseClassName}`;

  return (
    <div className={`${defaultClass} ${className || ''}`} onKeyPress={(e) => e.key === 'Enter' && onEnterKeyPress && onEnterKeyPress()}>
      <span className={`${defaultClass}__label`}>{label}</span>
      <Input
        value={value}
        className={`${defaultClass}__field`}
        type={type}
        placeholder={placeholder}
        onChange={onChange}
        disabled={disabled}
      />
    </div>
  );
};

TopLabelInputField.defaultProps = { isSP: true };
