import actionCreatorFactory from 'typescript-fsa';
import { ApiGetLocationImgParam } from './api/api-get-location-img/api-get-location-img';
import { ApiGeocoderParam } from './api/api-geocoder/api-geocoder';
import { Position, Address } from '../../type/map/map.type';

const ActionCreator = actionCreatorFactory('map');

export const MapActions = {
  api: {
    getLocationImg: ActionCreator<{ param: ApiGetLocationImgParam; callback:(url: string | null) => void;}>('/api/get/location/img'),
    geocoder: ActionCreator<{
      param: ApiGeocoderParam;
      callback?:(url: { position: Position, address: string; }) => void;
        }>('/api/geocoder'),
  },
  setCenterPos: ActionCreator<Position | null>('/set/center/pos'),
  setSearchPos: ActionCreator<Position | null>('/set/search/pos'),
  setSearchAddress: ActionCreator<Address | null>('/set/search/address'),
};
