import { useCallback, useEffect } from 'react';
import { useDispatch, useSelector } from 'react-redux';
import { useParams } from 'react-router-dom';
import { Button } from 'semantic-ui-react';
import { CustomerActions } from '../../../../redux/customer/customer.action';
import { openEmail } from '../../../../utilities/open-email';
import { IconButton } from '../../button/icon-button/icon-button';
import './customer-info-tab-contents.scss';
import { State } from '../../../../redux/root.reducer';

export const CustomerInfoTabContents = () => {
  /* Hooks */
  const { customerID } = useParams<{ customerID: string; }>();
  const customerInfo = useSelector((state:State) => state.customer.customer);
  const dispatch = useDispatch();

  /* Callback */
  const handleClickMail = useCallback(() => {
    openEmail({
      address: customerInfo?.mail_address || '',
      /* TODO  */
      subject: '',
      body: ['Test'],
    });
  }, []);

  /* Effect */
  useEffect(() => {
    dispatch(CustomerActions.api.customer.get({
      param: {
        id: 1,
      },
    }));
  }, [customerID]);

  return (
    <div className="CustomerInfoTabContents">
      <div className="CustomerInfoTabContents__row row1">
        <div className="col1">
          <div>ID</div>
          <div>{customerInfo?.id}</div>
        </div>
        <div className="col2">
          <Button onClick={() => {}}></Button>
        </div>
      </div>
      <div className="CustomerInfoTabContents__row row2">
        <div className="col1"></div>
        <div className="col2">{customerInfo?.name} </div>
      </div>
      <div className="CustomerInfoTabContents__row row3">
        <div className="col1"></div>
        <div className="col2">{customerInfo?.furigana}</div>
      </div>
      <div className="CustomerInfoTabContents__row row4">
        <div className="col1"></div>
        <div className="col2">
          <div className="row1">{customerInfo?.post_no}</div>
          <div className="row2">{customerInfo?.prefecture}{customerInfo?.city}{customerInfo?.address}</div>
          <div className="row3">{customerInfo?.building_name}</div>
        </div>
      </div>
      <div className="CustomerInfoTabContents__row row5">
        <div className="col1">TEL</div>
        <div className="col2">{customerInfo?.tel_no}</div>
      </div>
      <div className="CustomerInfoTabContents__row row6">
        <div className="col1"></div>
        <div className="col2">
          {customerInfo?.mail_address}
        </div>
        <div className="col3">
          <IconButton
            fontAwesomeClass="fas fa-envelope"
            onClick={handleClickMail}
          />
        </div>
      </div>
    </div>
  );
};
