import './right-label-checkbox.scss';
import React from 'react';

type Props = {
    label: string,
    className?: string,
} & React.InputHTMLAttributes<HTMLInputElement>;

export const RightLabelCheckbox = (props: Props) => {
  const {
    label, name, className,
  } = props;

  return (
    <div className={`RightLabelCheckbox ${className || ''}`}>
      <input className="RightLabelCheckbox__form" type="checkbox" name={`${name}`} />
      <label className="RightLabelCheckbox__label right" htmlFor={`${name}`}>{label}</label>
    </div>
  );
};
